.homeUser{
    display: flex;
    align-items: center;
    text-decoration: none;
    color: rgba(0, 0, 0, 0.500);
    transition: all 0.5s;
    border-radius: 10px;
}
.homeUser:hover{
    background-color: #38B2AC;
}

.homeUser > img{
    height: 3vmax;
    width: 3vmax;
    margin: 1vmax;
    border-radius: 100%;
    border: 3px solid rgb(255, 201, 201);
}